const button = document.querySelector('.search__btn')
const valueInput = document.querySelector('.search__input')
const textSearch = document.querySelector('.search__text')


const redirect = () => {
  window.location.href = './searchResults.html';

}

button.addEventListener('click', () => {
  // redirect()
  let value = valueInput.value

  if (value === '') {
    textSearch.innerHTML = 'Напишите  слово для поиска'
  } else {
    textSearch.innerHTML = `Не найдено совпадений для "` + value + `"`
  }

})
